import React, {useEffect,useState} from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { InputNumber,message } from 'antd';
import { createStructuredSelector } from 'reselect';
import ShoppingCartContext from '../../contexts/shoppingCart.context';
import { useContext } from 'react';

import './product.styles.css';
import { config } from "../../config";

import { selectCategories } from '../../redux/category/category.selector';

const Productpage = ({ categories, match }) => {
    const [messageApi, contextHolder] = message.useMessage();
    const success = () => {
        messageApi.open({
          type: 'success',
          content: 'Added to shopping cart',
        });
      };
    const product = [
        ...categories[0].products,
        ...categories[1].products,
        ...categories[2].products,
        ...categories[3].products
    ].find( ({_id}) => `${_id}` === match.params.productid );

    const [quantity,setQuantity] = useState(1);
    const { cartItems,handleCartItems } = useContext(ShoppingCartContext);

    const addToCart = () => {
        success();
        handleCartItems([
            ...cartItems,
            ...[{
                product_name: product.name,
                quantity: parseInt(quantity),
                price: product.price,
                total: Math.round(product.price * quantity * 100) / 100
            }]
        ]);
    }

    return <div className="container">
        {contextHolder}
        <div className="halfPage1">
            <div className="productName">{product.name}</div>
            <div className="pic">
                <img src={`https://duythai275.github.io/ludycakeshop/Cakes/${product._id}.jpg`} />
            </div>
        </div>
        <div className="halfPage">
            <div className="description">
                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
                <div>
                    <strong>Price:</strong><span> ${product.price}</span>
                </div>
            </div>

            <div>
                <div className="actions">
                    <div className="quantity">
                        <InputNumber addonBefore="Quantity" size="large" min={1} max={10} value={quantity} onChange={val => setQuantity(val)} />
                    </div>
                </div>
                <div className="actions">
                    <button type="button" class="addToCart" onClick={() => addToCart()}>
                        <span>Add to cart</span>
                    </button>
                </div>
            </div>
        </div>
    </div>
}

const mapStateToProps = createStructuredSelector({
    categories: selectCategories
});

export default withRouter(connect(mapStateToProps)(Productpage))