import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { Table,InputNumber,Input,message } from 'antd';
import ShoppingCartContext from '../../contexts/shoppingCart.context';
import { useContext } from 'react';

import './checkout.styles.css';
import { config } from "../../config";

import { selectCategories } from '../../redux/category/category.selector';

const { TextArea } = Input;

const Checkoutpage = ({ categories }) => {
    const [messageApi, contextHolder] = message.useMessage();
    const success = () => {
        messageApi.open({
          type: 'success',
          content: 'Order has been sent',
        });
      };

    const columns = [
        {
          title: 'Cake',
          dataIndex: 'product_name',
          key: 'product_name',
        },
        {
          title: 'Quantity',
          dataIndex: 'quantity',
          key: 'quantity',
        },
        {
          title: 'Total',
          dataIndex: 'total',
          key: 'total',
        },
        {
          title: 'Action',
          dataIndex: 'x',
          key: 'x'
        },
      ];

      const { cartItems,handleCartItems } = useContext(ShoppingCartContext);
      const [data,setData] = useState([]);
      const [subTotal,setSubTotal] = useState(0);

      useEffect(() => {
        setData(cartItems);
      }, [cartItems]);

      useEffect(() => {
        setSubTotal(data.reduce((acc,cur) => acc + cur.total,0));
      }, [data]);

    return <div className="checkoutContainer">
        {contextHolder}
        <div className="title">SHOPPING CART</div>
        <div className="customerSection">
            <table cellPadding={"10px"} width={"100%"}>
                <tr>
                    <td width="50%">
                        <div>Name</div>
                        <div>
                            <Input />
                        </div> 
                    </td>
                    <td width="50%">
                        <div>Phone</div>
                        <div>
                            <Input />
                        </div> 
                    </td>
                </tr>
                <tr>
                    <td>
                        <div>Email</div>
                        <div>
                            <Input />
                        </div> 
                    </td>
                    <td>
                        <div>Address</div>
                        <div>
                            <Input />
                        </div> 
                    </td>
                </tr>
                <tr>
                    <td colSpan={2}>
                        <div>Note</div>
                        <div>
                            <TextArea row={3} />
                        </div> 
                    </td>
                </tr>
            </table>
        </div>
        <br/>   
        <Table
            width="100%"
            pagination={false}
            columns={columns}
            dataSource={ data.map( (item,i) => {
                return {
                    key: i,
                    product_name: item.product_name,
                    quantity: <InputNumber min={1} value={item.quantity} onChange={ val => handleCartItems(cartItems.map( c => c.product_name === item.product_name ? 
                        ({
                            ...c,
                            quantity: val,
                            total: Math.round(item.price * val * 100) / 100
                        })
                        :
                        ({
                            ...c
                        })
                    ))}/>,
                    total: item.total,
                    x: <a onClick={() => handleCartItems(cartItems.filter( ({product_name}) => product_name !== item.product_name ))}>Delete</a>
                }
            })}
            footer={() => <div className="subtotal">
                <table>
                    <tr><td align='right' width="50%"><strong>Subtotal:</strong></td><td width="17%"></td><td>{subTotal}</td></tr>
                    <tr><td align='right'><strong>Tax:</strong></td><td></td><td>{Math.round(subTotal) / 10}</td></tr>
                    <tr><td align='right'><strong>Total:</strong></td><td></td><td>{Math.round(subTotal * 110) / 100}</td></tr>
                </table>
            </div>}
        />
        <div class="actionCheckout">
            <button type="button" class="addToCart" onClick={() => {
                handleCartItems([]);
                success();
            }}>
                <span>PLACE ORDER</span>
            </button>
        </div>
    </div>
}

const mapStateToProps = createStructuredSelector({
    categories: selectCategories
});

export default withRouter(connect(mapStateToProps)(Checkoutpage))